import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MaterialModule } from 'src/app/material';

import { DatepickerComponent } from './datepicker.component';

import { OwlDateTimeModule, OwlMomentDateTimeModule } from '@busacca/ng-pick-datetime';
import { UserDateFormatPipe } from 'src/app/shared/pipe/shared.pipe';
import { UserDateTimeFormatPipe } from 'src/app/shared/pipe/shared.pipe';
import { UserTimeFormatPipe } from 'src/app/shared/pipe/shared.pipe';

@NgModule({
  declarations: [DatepickerComponent, UserDateTimeFormatPipe, UserDateFormatPipe, UserTimeFormatPipe],
  imports: [
    CommonModule,
    FormsModule,
    MaterialModule,
    ReactiveFormsModule,
    OwlDateTimeModule,
    OwlMomentDateTimeModule
  ],
  exports     : [
    DatepickerComponent,
    UserDateTimeFormatPipe,
    UserDateFormatPipe,
    UserTimeFormatPipe,
    OwlDateTimeModule,
    OwlMomentDateTimeModule
  ],
  providers: [
    //{ provide: OWL_MOMENT_DATE_TIME_ADAPTER_OPTIONS, useValue: { useUtc: true } },
    //{ provide: DateTimeAdapter, useClass: MomentDateTimeAdapter, deps: [OWL_DATE_TIME_LOCALE] },
    //{ provide: DateTimeAdapter, useClass: CustomDateTimeAdapter },
    //{ provide: OWL_DATE_TIME_LOCALE, useValue: 'fr'}
  ],
  bootstrap: [
    DatepickerComponent
  ]
})
export class DatepickerModule {
  static forRoot(): ModuleWithProviders<DatepickerModule> {
    return {
      ngModule: DatepickerModule,
      providers: [DatePipe]
    };
  }
 }
