import { Component, OnInit, Input, ChangeDetectionStrategy, ChangeDetectorRef, ViewEncapsulation, ViewChildren, ViewChild, ElementRef, TemplateRef, ViewContainerRef, Output, EventEmitter, OnChanges, QueryList } from '@angular/core';
import { ControlContainer, NgForm } from '@angular/forms';

import * as moment from 'moment';
import _moment from "moment-timezone";

import { DateTimeAdapter } from '@busacca/ng-pick-datetime';
import { timeZones } from 'src/app/modules/profile/data/timezone/data';
import { SharedService } from 'src/app/shared/services/shared.service';


@Component({
  selector: 'app-datepicker',
  templateUrl: './datepicker.component.html',
  styleUrls: ['./datepicker.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})
export class DatepickerComponent implements OnInit {


  @Input() labelText?:string = '';
  @Input() fieldRequired:string;
  @Input() fieldName:string;
  @Input() type:string;
  @Input() fieldClass?:string = '';
  @Input() fieldReadonly?:boolean = false;
  @Input() isHour12Timer?:boolean = true;
  @Input() fieldModel?:any;
  @Input() min?:any;
  @Input() max?:any;
  @Input() isDisabled?:boolean = false;

  @Output() public fieldParams = new EventEmitter();

  @ViewChildren('dateTimeElem') components:QueryList<DatepickerComponent>;

  timeZone:any;
  index:any;

  timeZonesData:any = timeZones;

  selectedDate:any;

  isPickerOpened:boolean = false;

  constructor(
    private dateTimeAdapter: DateTimeAdapter<any>,
    private sharedService: SharedService
  ) { 

  }

  isFieldRequired(){
    return this.fieldRequired == "required" ? true : false;
  }

  onPickerChange() {
    this.fieldParams.emit(moment.utc(this.selectedDate))
  }

  camelCase(str) {
    return str.replace(/(?:^\w|[A-Z]|\b\w)/g, (word, index) => {
      return index === 0 ? word.toLowerCase() : word.toUpperCase();
    }).replace(/\s+/g, '');
  }



  ngOnInit(): void {

    this.index = this.sharedService.guid().replace(/-/g, '');

    if(this.fieldModel != undefined) {
      this.selectedDate = moment.utc(this.fieldModel).tz(this.timeZone.region)
      this.fieldParams.emit(moment.utc(this.selectedDate))
    }
    else {
      this.selectedDate = moment().tz(this.timeZone.region)
    }

  }

  ngOnChanges(): void {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    if(this.fieldModel != undefined) {
      this.selectedDate = moment.utc(this.fieldModel).tz(this.timeZone.region)
    }
    else {
      this.selectedDate = moment().tz(this.timeZone.region)
    }

    if(this.min != null){
      this.min = moment.utc(this.min).tz(this.timeZone.region)
    }

    if(this.max != null){
      this.max = moment.utc(this.max).tz(this.timeZone.region)
    }
    

    this.dateTimeAdapter.setLocale(this.timeZone.language)
    
  }

}
